import React, { Component} from "react"
import Opcion from './Opcion.js'
import {
  Container,
  Card,
  Button,
  Modal,
  Form,
  ListGroup,
  Table,
  Row,
  Col
} from "react-bootstrap";


class Producto extends Component{
  constructor(){
    super();
    this.state={
    idp:"",
    nombreProducto: "",
    descripcion: "",
    precio:"",
    nombreOpcion:"",
    precioAdiconal:"",
    modalActualizar: false,
    mostrarOpciones:false}
  this.handleInput=this.handleInput.bind(this)
  this.handleSubmit=this.handleSubmit.bind(this)
  this.mostrarModalActualizar=this.mostrarModalActualizar.bind(this)
  this.cerrarModalActualizar=this.cerrarModalActualizar.bind(this)
  this.handleEditName=this.handleEditName.bind(this)
  this.mostrarOpciones=this.mostrarOpciones.bind(this)
  
 }  
    handleRemoveSeccion(id){
        this.props.remove(id)
      }
    handleInput(e){
    const {value, name} =e.target 
    this.setState({
      [name]: value
    })
    }
    handleSubmit(ids,idp){
      const id=Math.floor(Math.random() * 10000) + 1
      const nuevaopcion={ido:id,nombreOpcion: this.state.nombreOpcion, precioAdiconal:this.state.precioAdiconal}
      this.props.addopcion(ids,idp,nuevaopcion)
    }    
    handleREmoveProducto(id){
        const index=this.props.ids
        this.props.removep(index,id)
      }  

      mostrarModalActualizar(producto){
        this.setState({ 
          idp:producto.idp,
          nombreProducto:producto.nombreProducto,
          descripcion:producto.descripcion,
          precio: producto.precio,
          modalActualizar: true });
      };
  
      cerrarModalActualizar(){
        this.setState({ modalActualizar: false });
      };
      handleEditName(){
        this.props.cambio(this.props.ids,this.state.idp,this.state.nombreProducto,this.state.descripcion,this.state.precio)
        this.setState({modalActualizar:false})
      }
      mostrarOpciones(){
        const mp=this.state.mostrarOpciones
        this.setState({ 
          mostrarOpciones: !mp});
      };
    render(){
        const producto= this.props.producto.map((producto,id)=>{return (
          <Container>
            <Row > 
            <Col><h5>{producto.nombreProducto}</h5></Col> 
            <Col><h5>{producto.precio}</h5></Col>
            <Col>
            <Button variant="secondary" size="sm" onClick={this.mostrarModalActualizar.bind(this,producto)}>Editar</Button>{' '}
            <Button variant="primary" size="sm" onClick={this.mostrarOpciones.bind(this)}>Opciones</Button>  {' '}
            <Button variant="danger" size="sm" onClick={this.handleREmoveProducto.bind(this,id)}>Borrar</Button> </Col>   
            </Row >
        <Modal show={this.state.modalActualizar} >
          <Modal.Header><div><h3>Editar Registro</h3></div> </Modal.Header>
          <Modal.Body ><Form.Group>
              <label>Nombre Producto: </label>
              <input className="form-control" name="nombreProducto" type="text" onChange={this.handleInput} placeholder={this.state.nombreProducto}/>
            </Form.Group>
            <Form.Group>
              <label>Descripcion: </label>
              <input className="form-control"name="descripcion"type="text"onChange={this.handleInput}placeholder={this.state.descripcion}/>
            </Form.Group>
            <Form.Group>
              <label>Precio: </label>
              <input className="form-control"name="precio" type="number" min="0"onChange={this.handleInput}placeholder={this.state.precio}/>
            </Form.Group>
          </Modal.Body>
          <Modal.Footer>
            <Button color="primary"onClick={() => this.handleEditName()}>Editar</Button>
            <Button color="danger"onClick={() => this.cerrarModalActualizar()}>Cancelar</Button>
          </Modal.Footer>
        </Modal>
        <Opcion isLoggedIn={this.state.mostrarOpciones} opciones={producto.opciones} ids={this.props.ids} idp={id} remove={this.props.removeo}/>
        <Form.Group>
                    <input type="text" onChange={this.handleInput} name="nombreOpcion" className="form-control" placeholder="Nombre de la opción"></input>
                    <input type="number" min="0" onChange={this.handleInput} name="precioAdiconal" className="form-control" placeholder="Precio Adicional"></input>
                    <button  type="button" className="btn btn-primary" onClick={this.handleSubmit.bind(this,this.props.ids,id)}>Agregar Nueva Opción</button>    
        </Form.Group>
        </Container>
                
            )     
        })
        const producto2= this.props.producto.map((producto,id)=>{return (
          <Container>
            <Row > 
            <Col><h5>{producto.nombreProducto}</h5></Col> 
            <Col><h5>{producto.precio}</h5></Col>
            <Col>
            <Button variant="secondary" size="sm" onClick={this.mostrarModalActualizar.bind(this,producto)}>Editar</Button>{' '}
            <Button variant="primary" size="sm" onClick={this.mostrarOpciones.bind(this)}>Opciones</Button>  {' '}
            <Button variant="danger" size="sm" onClick={this.handleREmoveProducto.bind(this,id)}>Borrar</Button> </Col>   
            </Row >
        <Modal show={this.state.modalActualizar} >
          <Modal.Header><div><h3>Editar Registro</h3></div> </Modal.Header>
          <Modal.Body ><Form.Group>
              <label>Nombre Producto: </label>
              <input className="form-control" name="nombreProducto" type="text" onChange={this.handleInput} placeholder={this.state.nombreProducto}/>
            </Form.Group>
            <Form.Group>
              <label>Descripcion: </label>
              <input className="form-control"name="descripcion"type="text"onChange={this.handleInput}placeholder={this.state.descripcion}/>
            </Form.Group>
            <Form.Group>
              <label>Precio: </label>
              <input className="form-control"name="precio" type="number" min="0"onChange={this.handleInput}placeholder={this.state.precio}/>
            </Form.Group>
          </Modal.Body>
          <Modal.Footer>
            <Button color="primary"onClick={() => this.handleEditName()}>Editar</Button>
            <Button color="danger"onClick={() => this.cerrarModalActualizar()}>Cancelar</Button>
          </Modal.Footer>
        </Modal>
        </Container> )     
        })
if(this.state.mostrarOpciones)
{return(producto)}
return(producto2)

}}
export default Producto;