import React, { Component} from "react"
import Producto from './Producto.js'
import "bootstrap/dist/css/bootstrap.min.css";
import {
  Container,
  Card,
  Button,
  Modal,
  FormGroup,
  Row,
  Col,
  Form
} from "react-bootstrap";

class Seccion extends Component{
  constructor(){
    super();
    this.state={
    ids:"",
    nombreSeccion:"",
    nombreProducto: "",
    descripcion: "",
    precio:"",
    opciones:[],
    modalActualizar: false,
    mostrarProductos: true}
  this.handleInput=this.handleInput.bind(this)
  this.handleSubmit=this.handleSubmit.bind(this)
  this.mostrarModalActualizar=this.mostrarModalActualizar.bind(this)
  this.cerrarModalActualizar=this.cerrarModalActualizar.bind(this)
  this.handleEditName=this.handleEditName.bind(this)
  this.mostrarProductos=this.mostrarProductos.bind(this)

}  
    handleRemoveSeccion(id){
        this.props.remove(id)
      }
    handleInput(e){
    e.preventDefault()
    const {value, name} =e.target 
    this.setState({
      [name]: value
    })
    }
    handleSubmit(id){
      const idp=Math.floor(Math.random() * 10000) + 1
      const nuevoproducto={idp:idp,nombreProducto: this.state.nombreProducto, descripcion: this.state.descripcion, precio:this.state.precio, opciones: this.state.opciones}
      this.props.addproducto(id,nuevoproducto)
    }  
    mostrarModalActualizar(menu){
  
      this.setState({ 
        ids:menu.ids,
        nombreSeccion:menu.nombreSeccion,
        modalActualizar: true })
    };

    cerrarModalActualizar(){
      this.setState({ modalActualizar: false });
    };
    handleEditName(){
      this.props.cambios(this.state.ids,this.state.nombreSeccion)
      this.setState({modalActualizar:false})
    }
  
    mostrarProductos(){
      const mp=this.state.mostrarProductos
      this.setState({ 
        mostrarProductos: !mp});
    };
    render(){
        const secciones= this.props.seccion.map((menu,id)=>
        {
      
        return (
          <Container>
          <Card key={menu.ids}>
          <Card.Header>
            <Container>
            <Row>
          <Col><h3 className="col">{menu.nombreSeccion}</h3></Col>               
          <Col>
          <Button variant="secondary" onClick={this.mostrarModalActualizar.bind(this,menu)}>Editar Nombre</Button> {' '}
          <Button variant="primary" onClick={this.mostrarProductos.bind(this)}>Ocultar/Mostrar Productos</Button> {' '}
          <Button  variant="danger" onClick={this.handleRemoveSeccion.bind(this,id)}>Borrar Sección </Button></Col>  
          </Row>
          </Container>

          </Card.Header>
          <Modal show={this.state.modalActualizar} >
          <Modal.Header>
           <div><h3>Editar Registro</h3></div>
          </Modal.Header>
          <Modal.Body >
            <FormGroup>
              <label>
                Nombre seccion: 
              </label>
              <input
                className="form-control"
                name="nombreSeccion"
                type="text"
                onChange={this.handleInput}
                placeholder={this.state.nombreSeccion}
              />
            </FormGroup>
          </Modal.Body>

          <Modal.Footer>
            <Button
              variant="success"
              onClick={() => this.handleEditName()}
            >
              Editar
            </Button>
            <Button
              variant="danger"
              onClick={() => this.cerrarModalActualizar()}
            >
              Cancelar
            </Button>
          </Modal.Footer>
        </Modal>
            </Card>
            </Container>
          )     
    })
    const secciones2= this.props.seccion.map((menu,id)=>
    {
  
    return (
      <Container>
      <Card key={menu.ids}>
      <Card.Header>
        <Container>
        <Row>
      <Col><h3 className="col">{menu.nombreSeccion}</h3></Col>               
      <Col>
      <Button variant="secondary" onClick={this.mostrarModalActualizar.bind(this,menu)}>Editar Nombre</Button> {' '}
      <Button variant="primary" onClick={this.mostrarProductos.bind(this)}>Ocultar/Mostrar Productos</Button> {' '}
      <Button  variant="danger" onClick={this.handleRemoveSeccion.bind(this,id)}>Borrar Sección </Button></Col>  
      </Row>
      </Container>
      </Card.Header>
      <Modal show={this.state.modalActualizar} >
      <Modal.Header>
       <div><h3>Editar Registro</h3></div>
      </Modal.Header>
      <Modal.Body >
        <FormGroup>
          <label>
            Nombre seccion: 
          </label>
          <input
            className="form-control"
            name="nombreSeccion"
            type="text"
            onChange={this.handleInput}
            placeholder={this.state.nombreSeccion}
          />
        </FormGroup>
      </Modal.Body>
      <Modal.Footer>
        <Button
          variant="success"
          onClick={() => this.handleEditName()}
        >
          Editar
        </Button>
        <Button
          variant="danger"
          onClick={() => this.cerrarModalActualizar()}
        >
          Cancelar
        </Button>
      </Modal.Footer>
    </Modal>       
    <Card.Body>
          <Producto  isLoggedIn={this.state.mostrarProductos} cambio={this.props.cambiop} producto={menu.productos} removep={this.props.removeproducto} ids={id} removeo={this.props.removeopcion} addopcion={this.props.addOpcion} />
          <Form.Group>
<input type="text" onChange={this.handleInput} name="nombreProducto" className="form-control" placeholder="Nombre de Producto"></input>
<input type="text" onChange={this.handleInput} name="descripcion" className="form-control" placeholder="Descripción de Producto"></input>
<input type="number" min="0" onChange={this.handleInput} name="precio" className="form-control" placeholder="Precio"></input>
<Button  type="button" color="primary" onClick={this.handleSubmit.bind(this,id)}>Agregar Nuevo Producto</Button> 
</Form.Group>
    </Card.Body>  
        </Card>
        </Container>
      )     
})

if(this.state.mostrarProductos){
  return(

    
secciones

)}
return( secciones2)
}}
export default Seccion;